.modal-window__locations, .modal-window__franchise {
  width: calc(100% - 40px);
  background-color: var(--txt-color-btnwhi);
  border: 2px solid var(--button-color-yel);
  min-height: 365px;
  border-radius: 10px;
  padding: 28px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-window__close-btn {
  width: 21px;
  height: 21px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 4px;
  right: 4px;
}

.modal-window__close-icon {
  fill: var(--txt-color-blck);
  transition: fill var(--main-transition-rule);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.modal-window__close-btn:hover .modal-window__close-icon, .modal-window__close-btn:focus .modal-window__close-icon {
  fill: var(--button-color-yel);
}

@media screen and (min-width: 480px) {
  .modal-window__locations, .modal-window__franchise {
    width: 440px;
  }
}

@media screen and (min-width: 768px) {
  .modal-window__locations, .modal-window__franchise {
    width: 568px;
    padding: 32px 60px;
  }

  .modal-window__close-btn {
    width: 24px;
    height: 24px;
    top: 16px;
    right: 16px;
  }
}

